.loading {
  height: 100vh;
  width: 100%;
  z-index: 99;
  background-color: #0b134f;
  place-items: center;
  font-family: Orbitron, sans-serif;
  display: grid;
  position: absolute;
  inset: 0;
}

.loading__box {
  width: 500px;
  height: 200px;
  border: 3px solid #6cff8c7a;
  border-color: #6cff8c7a #6cff8d;
  position: relative;
}

@media (max-width: 600px) {
  .loading__box {
    width: 90vw;
  }
}

.loading__bar {
  width: 90%;
  height: 10px;
  background: #ccc;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.loading__bar--inner {
  height: 100%;
  width: 50%;
  background: #6cff8d;
  border-radius: 2px;
}

.loading__text {
  position: relatiove;
  color: #fff;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-size: 20px;
  font-weight: 700;
  display: flex;
}

@media (max-width: 355px) {
  .loading__text {
    font-size: 17px;
  }
}

.loading__text--dot {
  width: 15px;
  height: 15px;
  background: #fff;
  border-radius: 50%;
  margin: 0 3px;
  animation: pulse .8s infinite;
}

@keyframes pulse {
  from {
    opacity: 0;
    background: #6cff8d;
  }

  to {
    opacity: 1;
  }
}

.loading__text--border {
  width: 85%;
  height: 1px;
  background: #6cff8c7a;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translateX(-50%);
}

.loading__counter {
  color: #fff;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 70%;
  left: 0;
}

.loading__counter--number {
  color: #6cff8c;
}

.loading__width_animation {
  animation: width_animation 1s forwards;
}

@keyframes width_animation {
  0% {
    width: 85%;
  }

  100% {
    width: 280px;
  }
}

.loading__svg {
  transform: transalte(-50%, -50%);
  width: 85%;
  opacity: 0;
  position: absolute;
  top: 8%;
  left: 8%;
}

#btn-back-to-top {
  z-index: 99;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.landing {
