}

.landing .header {
  z-index: 2;
  font-family: Josefin Sans, sans-serif;
  font-size: 21px;
  position: absolute;
  top: -100%;
  left: 50%;
  transform: translateX(-50%);
}

.landing .header__wrap {
  width: 60vw;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.landing .header__left {
  align-items: center;
  gap: 2rem;
