@media (max-width: 1200px) {
  .landing .header__wrap {
    width: 100vw;
  }
}

@media (max-width: 676px) {
  .landing .header__sm {
    display: none;
  }
}

@media (max-width: 371px) {
  .landing .header {
    left: 36%;
  }

  .landing .header__wrap {
    gap: 20%;
  }

  .landing .header .coolLink[data-text="Home"] {
    display: none;
  }
}

.landing .socials {
  z-index: 2;
  align-items: center;
  gap: 1rem;
  display: flex;
  position: absolute;
  bottom: -100%;
  left: 50%;
  transform: translateX(-50%);
}

.pkTopBtn {
  z-index: 99;
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 15px;
  font-size: 18px;
  position: absolute;
  bottom: 20px;
  right: 30px;
}

.centered_link {
  cursor: pointer;
  font-size: 40px;
  position: absolute;
  top: 40%;
  left: 10%;
}

.scrollDown {
  position: absolute;
  bottom: -100%;
  left: 50%;
  transform: translateX(-50%);
}

.scrollDown__wheel {
  height: 42px;
  width: 24px;
  border: 2px solid #fff;
  border-radius: 10px;
}

.scrollDown__wheel--inner {
  width: 4px;
  height: 4px;
  background: #fff;
  border-radius: 50%;
  margin-top: 5px;
  animation: movingWheel .5s infinite;
  position: absolute;
  left: 10px;
}

@keyframes movingWheel {
  0% {
    top: 1px;
  }

  25% {
    top: 2px;
  }

  50% {
    top: 3px;
  }

  75% {
    top: 2px;
  }

  100% {
    top: 1px;
  }
}

.scrollDown .scrollDown__arrows {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.scrollDown .scrollDown__arrows .scrollDown__arrow {
  width: 15px;
  height: 15px;
  border-bottom: 2px solid #fff;
  border-right: 2px solid #fff;
  animation: scrollAnim .75s infinite alternate;
  transform: rotate(45deg);
}

.scrollDown .scrollDown__arrows .scrollDown__arrow:nth-child(1) {
  animation-delay: .1s;
}

.scrollDown .scrollDown__arrows .scrollDown__arrow:nth-child(2) {
  animation-delay: .2s;
}

.scrollDown .scrollDown__arrows .scrollDown__arrow:nth-child(3) {
  animation-delay: .3s;
}

@keyframes scrollAnim {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 1;
  }
}

.intro {
  width: 100%;
