.coolLink {
  place-items: center;
  display: grid;
  position: relative;
  overflow: hidden;
}

.coolLink:before {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100%;
  background: currentColor;
  transition: transform .2s cubic-bezier(.5, .5, .3, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scale3d(0, 1, 1);
}

.coolLink:after {
  content: attr(data-text);
  height: 100%;
  transition: transform .2s cubic-bezier(.5, .5, .3, 1);
  position: absolute;
  top: 0;
  transform: translate3d(125%, 0, 0);
}

.coolLink span {
  transition: transform .2s cubic-bezier(.5, .5, .3, 1);
}

.coolLink:hover:before {
  transform-origin: 0;
  transform: scale3d(1, 1, 1);
}

.coolLink:hover:after {
  transform: translate3d(0, 0, 0);
}

.coolLink:hover span {
  transform: translate3d(-150%, 0, 0);
}

.coolButton {
  cursor: pointer;
  width: 175px;
  height: 120px;
  color: #fff;
  background: none;
  border: none;
  margin-top: 17.5px;
  position: relative;
}

.coolButton:before, .coolButton:after {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.coolButton:before {
  content: "";
  clip-path: path("M154.5,88.5 C131,113.5 62.5,110 30,89.5 C-2.5,69 -3.5,42 4.5,25.5 C12.5,9 33.5,-6 85,3.5 C136.5,13 178,63.5 154.5,88.5 Z");
  background: #fff;
  transition: clip-path .5s cubic-bezier(.585, 2.5, .645, .55);
}

.coolButton:hover:before {
  clip-path: path("M143,77 C117,96 74,100.5 45.5,91.5 C17,82.5 -10.5,57 5.5,31.5 C21.5,6 79,-5.5 130.5,4 C182,13.5 169,58 143,77 Z");
}

.coolButton span {
  mix-blend-mode: difference;
  font-weight: 525;
}

.coolButton:after {
  content: "";
  height: 85%;
  width: 95%;
  border: 1px solid #fff;
  border-radius: 58% 42% 55% 45% / 56% 45% 55% 44%;
  transition: transform .5s cubic-bezier(.585, 2.5, .645, .55);
  top: 6px;
  transform: rotate(-20deg);
}

.coolButton:hover:after {
  transform: translate3d(0, -5px, 0);
}

@media (max-width: 801px) {
  .coolButton {
    font-size: 23px;
